export const formatTime = date => {
  const year = date.getFullYear()
  const month = date.getMonth() + 1
  const day = date.getDate()
  const hour = date.getHours()
  const minute = date.getMinutes()
  const second = date.getSeconds()

  return [year, month, day].map(formatNumber).join('/') + ' ' + [hour, minute, second].map(formatNumber).join(':')
}

export const formatNumber = n => {
  n = n.toString()
  return n[1] ? n : '0' + n
}

/**
 *
 * @param {*} str
 * @param {*} name
 */
export const GetQueryByString = (str, name) => {

  //
  var tempArr = str.split('?');
  console.log(tempArr)
  // //1
  if (tempArr.length - 1 > 1) {
    var rt = null;
    for (var i in tempArr) {
      var s = tempArr[i]
      var reg1 = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //
      var r1 = s.match(reg1); //
      if (r1 != null) {
        rt = decodeURIComponent(r1[2]);//
      }
    }

    return rt
  }

  var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //
  if (!str.split("?")[1]) return null;
  var r = str.split("?")[1].match(reg); //
  console.log(r)
  if (r != null) {
    return decodeURIComponent(r[2]);

  }
  return null; //

}


export const ls = {
  set(key, val) {
    //Null undefined '' 
    if(!val && (val!=0 || val!= false))return false;
    return  uni.setStorageSync(key, JSON.stringify(val))
  },

  get(key) {
    var val = uni.getStorageSync(key);
    if(!val) return false;
    try{
      return JSON.parse(val)
    }catch (e) {
      return false;
    }

  },
  remove(key) {
    return uni.removeStorageSync(key)
  },
  clear() {
    return uni.clearStorageSync()
  }
};
